import { Component, OnInit } from '@angular/core';
import { CommentService } from 'src/app/shared/comments/comment/comment.service';
import { Comment } from 'src/app/shared/comments/comment-model/comment';

import { AngularFirestore } from '@angular/fire/firestore';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'comment-list',
  templateUrl: './comment-list.component.html',
  styleUrls: ['./comment-list.component.css']
})
export class CommentListComponent implements OnInit {
  list:Comment[];

  constructor( private service : CommentService, private fireStore : AngularFirestore, private notiFication : ToastrService ) { }

  ngOnInit() {
    this.getData();
  }

  getData(){
    this.service.getData()
    .subscribe(data => {
     this.list  = data.map(item =>{

       return {
         id: item.payload.doc.id,
         ...item.payload.doc.data()
       }as Comment;
       });
    });
  }

  onEdit( com:Comment ){
      this.service.formData = Object.assign({},com);

  }

  onDelete( id ){
    if(confirm("Are you sure to delete this record"))
      this.fireStore.doc('chats/'+ id).delete();
      this.notiFication.warning("Deleted successfully")
  }

}
