import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';

import { auth } from 'firebase';
import { firebase } from '@firebase/app';
import { AngularFirestore } from '@angular/fire/firestore';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
// import firebase = require('firebase');

@Injectable({
  providedIn: 'root'
})
export class LoginService {
newuser:any;
eventautherror = new BehaviorSubject<string>(" ");
eventautherror$ = this.eventautherror.asObservable();
  constructor( private afAuth:AngularFireAuth, private db: AngularFirestore, private router :Router) { }

  // user login with google i
  login(){
    this.afAuth.auth.signInWithRedirect(new auth.GoogleAuthProvider());

  }

  // user login with google id
  loginfb(){
    this.afAuth.auth.signInWithRedirect(new auth.FacebookAuthProvider());

  }

  getuserstate(){
    return this.afAuth.authState;
  }

  loginemail(email:string, password:string){
    this.afAuth.auth.signInWithEmailAndPassword(email,password)
    .catch(error =>{
      this.eventautherror.next(error);
    })
    // you can vcall from service also
    // .then(()=>{
    //   this.router.navigate(['/']);
    // })
    }
  // sign-up page - create a new user
    createuser(user){
        this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.password)
        .then(userCredential =>{
            this.newuser = user;

            userCredential.user.updateProfile({
              displayName:user.firstName+' '+user.lastName
            });
              this.insertUserdata(userCredential);
            // this.insertUserdata(userCredential)
            //   .then(()=>{
            //     this.router.navigate(['/']);
            //   })

        })
          .catch(error =>{
            this.eventautherror.next(error);
          })
    }

    insertUserdata(userCredential:firebase.auth.UserCredential){
          return this.db.doc(`User/${userCredential.user.uid}`).set({
            email:this.newuser.email,
            firstName:this.newuser.firstName,
            lastName:this.newuser.lastName,
            role:'network user'
          })
    }

//user logout
  logout(){
    this.afAuth.auth.signOut();
  }

  // getLoggedInUser(){
  //   return this.afAuth.authState;
  // }
}
